import { CompareResult } from './types.js';
import { clean, compareVersion, validateWithError } from './utils.js';

/**
 * @description
 * Determines if verA is greater than or equal to verB.
 *
 * @param verA - The first version to compare.
 * @param verB - The second version to compare.
 * @returns True if verA is greater than or equal to verB, false otherwise.
 *
 * @docsCategory preference/utils/semver
 * @docsPage semver-gte
 * @codeFilter gte
 */
export const gte = (verA: string, verB: string): boolean => {
  if (validateWithError(verA, verB)) {
    const result = compareVersion(clean(verA), clean(verB));
    return (
      result === CompareResult.GreaterThan || result === CompareResult.Equal
    );
  }
  return false;
};
